import React from 'react';
import Topform from './Topform'
import Logo from './components/Logo';
const Topsection = () => (
  <div>
	    <div className="main-container">		
		<div className="domain-green alert-top text-center">
			This domain belongs to the Global Ventures network. We have interesting opportunities for work, sponsors and partnerships.&nbsp; 
			<span className="text-service">
				<a className="label badge-warning" href={'https://domaindirectory.com/servicepage/?domain='+window.Configs.domain} target="_blank" rel="noopener noreferrer">
					&nbsp;Inquire now&nbsp;
				</a>
			</span>
		</div>
		
		<div className="top-section-container bg-overlay">
			<div className="container ts-desc text-center mb-5">	
				<div className="sec-logo text-center">
					<Logo />
				</div>
				<h1 className="fw-800 mt-3">				
					{window.Configs.description}
				</h1>
			</div>
			<div className="container">
				<div className="row">
					<div className="col-md-4">
						<div className="media">							
							<div className="ts-icon mr-3">
								<i className="fas fa-globe"></i>
							</div>
							<div className="media-body">
								<h5 className="mt-0 fw-600">Contrib Marketplace</h5>
								<p>
									Browse Jobs, Ideas and Micro Tasks. 
								</p>
							</div>
						</div>
					</div>
					<div className="col-md-4">
						<div className="media">							
							<div className="ts-icon mr-3">
								<i className="fas fa-globe"></i>
							</div>
							<div className="media-body">
								<h5 className="mt-0 fw-600">Contribute</h5>
								<p>
									Contribute using your skills, services, apps and/or capital. 
								</p>
							</div>
						</div>
					</div>
					<div className="col-md-4">
						<div className="media">							
							<div className="ts-icon mr-3">
								<i className="fas fa-globe"></i>
							</div>
							<div className="media-body">
								<h5 className="mt-0 fw-600">Crypto Marketplace</h5>
								<p>
									Contribute to blockchain projects on premium urls today 
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div className="container mt-4">
				<div className="row justify-content-center">
					<div className="col-md-8">
						<h5 className="text-center mb-4">
							We envision people around the world with complementary skills, passion, time and resources coworking online with targeted premium assets just like {window.Configs.domain}				
						</h5>						
					</div>
				</div>
				<div className="row justify-content-center">
					<div className="col-md-6">
						<div className="top-form-content">
							<Topform />						
						</div>
					</div>
				</div>
			</div>
		</div>
		
		<div className="section-feat-intro sec-stats">
			<div className="container feat-intro shadow-1 p-60px">
				<div className="row">
					<div className="col-sm-12">
						<ul className="row">
							<li className="col">
								<div className="no-of-stats">5250</div>
								<div>NO. OF MEMBERS</div>
							</li>
							<li className="col">
								<div className="no-of-stats">310</div>
								<div>NO. OF CAMPAIGNS</div>
							</li>
							<li className="col">
								<div className="no-of-stats">395</div>
								<div>REWARDS GIVEN</div>
							</li>
							<li className="col">
								<div className="no-of-stats">3599072</div>
								<div>TOTAL IMPRESSIONS</div>
							</li>							
						</ul>
					</div>
				</div>
			</div>
		</div>
		
	</div>
  </div>
)
 
export default Topsection;

